<template>
    <div>
        <headers :title="title"></headers>
        <div class="add-content">
            <ul>
                <li>
                    <div class="detail">
                        <div>郭功军 133****2893</div>
                        <p>重庆重庆市江北区 五里店</p>
                    </div>
                    <div class="teche clearfix">
                       <van-radio class="fl radioType" name="1" v-model="radio">默认地址</van-radio>
                       <font class="fr add-edit">编辑</font><i class="fr add-icon2"></i><font class="fr add-edit">删除</font><i class="fr add-icon1"></i>
                    </div>
                </li>
            </ul>
        </div>
        <footers :footerData="footerData"></footers>
    </div>
    
</template>

<style lang="less">
    .add-content{
        font-family: "PingFang-SC-Medium";
        padding-top: 90px;
        background: #fff;
        width: 750px;
        ul{
            padding-left: 32px;
            li{                 
                .detail{
                    height: 158px;
                    border-bottom: 1px solid #D4D4D6;
                    div{
                        padding-top: 28px;
                        font-size: 32px;
                        color: #000;
                    }
                    p{
                        padding-top: 26px;
                        font-size: 28px;
                        color: #777;
                    }
                }
                .teche{
                    padding-right: 10px;
                    height: 72px;
                    padding-top: 22px;
                    .van-radio__input{
                        position: absolute;
                        width: 60px;
                    }
                    .van-radio__label{
                        font-size: 30px!important;
                        color: rgb(245,60,24)!important;
                    }
                    .van-radio .van-icon-checked{
                        position: absolute;
                        left: 0px;
                        width: 40px;
                    }
                    .van-radio__label{
                        position: absolute;
                        left: 66px;
                        
                    }
                    .add-edit{
                        padding-top: 5px;
                        padding-right: 35px;
                        font-size: 27px;
                        color: rbg(119,119,119)
                    }
                    .add-icon1{
                        width: 40px;
                        height: 40px;
                        background: url("../../assets/imgs/add-delete.png") no-repeat;
                        background-size: 100% 100%;
                        margin-right: 12px;
                        margin-top: 2px;
                    }
                    .add-icon2{
                        width: 36px;
                        height: 37px;
                        background: url("../../assets/imgs/add-edit.png") no-repeat;
                        background-size: 100% 100%;
                        margin-right: 12px;
                        margin-top: 4px;
                    }
                }
            }
        }
       
    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
import footers from '../../components/editfooter.vue'
export default {
  components:{
      headers,
      footers
  },
  data(){
      return{
        title: "地址列表",
        radio: '1',
        footerData: "新增地址"
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(247,246,247)";
  }
}
</script>





